import React, { useEffect, useState } from "react";
import style from "./style.module.css";
import DashboardLout from "@/layout/dashboarLayout/indexDash";
import UserLayout from "@/layout/userLayout";
import { useDispatch, useSelector } from "react-redux";
import { BASE_URL } from "@/config";
import { getAllPost } from "@/config/redux/action/postAction";
import { getUserProfile } from "@/config/redux/action/authAction";
import { headers } from "next/headers";

const ProfilePage = () => {
  const [userProfile, setUserProfile] = useState({});
  const [userPost, setUserPost] = useState([]);

  const authState = useSelector((state) => state.auth);
  const postState = useSelector((state) => state.postReducer);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getUserProfile({ token: localStorage.getItem("token") }));
    dispatch(getAllPost());
  }, []);

  useEffect(() => {
    if (authState.user != undefined) {
      setUserProfile(authState.user);

      const post = postState.post.filter((post) => {
        return post.userId.username === authState.user.userId.username;
      });
      setUserPost(post);
    }
  }, [authState.user, postState.pos]);

  const updateProfilePic = async (file) => {
    const formData = new FormData();
    formData.append("profilePicture", file);
    formData.append("token", localStorage.getItem("token"));

    const response = await clientServer.post("/updateProfilePic", {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
  };

  return (
    <UserLayout>
      <DashboardLout>
        {authState.user && userProfile.userId && (
          <div className={style.container}>
            <div className={style.backDropContianer}>
              <label
                onChange={(e) => updateProfilePic.e.file}
                htmlFor="profilePicUpload"
                className={style.backDrop_overlay}
              >
                <p>Edit</p>
              </label>
              <input hidden type="file" id="profilePicUpload" />
              <img
                src={`${BASE_URL}/${userProfile.userId.profilePicture}`}
                className={style.backDrop}
              />
            </div>
            <div className={style.profileContianer_details}>
              <div
                style={{
                  display: "flex",
                  gap: "1.2rem",
                }}
              >
                <div style={{ flex: " 0.7" }}>
                  <div
                    style={{
                      display: "flex",
                      width: "fit-content",
                      alignItems: "center",
                    }}
                  >
                    <h4>{userProfile.userId.name} </h4>
                    <p style={{ color: "gray", marginLeft: " 0.5rem" }}>
                      @{userProfile.userId.username}{" "}
                    </p>
                  </div>

                  <div>
                    <p>{userProfile.bio} </p>
                  </div>

                  <div className="workHistory">
                    <h3 style={{ marginTop: " 2rem" }}>Work History :-</h3>

                    <div className={style.workHistoryPage}>
                      {userProfile.pastWork.map((work, index) => {
                        return (
                          <div className={style.workHistoryCard} key={work._id}>
                            <div
                              style={{
                                fontWeight: "bold",
                                display: "flex",
                                alignItems: "center",
                                gap: " 0.8rem",
                                marginLeft: "0.5rem",
                              }}
                            >
                              {work.company}
                              <p>{work.position} </p>
                            </div>
                            <p>{work.years} </p>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </div>

                <div style={{ flex: "0.3" }}>
                  <div>
                    <h4>Recent Activity</h4>
                    {userPost.map((post, index) => {
                      return (
                        <div key={index._id} className={style.postCard}>
                          <div className={style.card}>
                            <div className={style.card_ProfileContainer}>
                              <img src={`${BASE_URL}/${post.media}`} alt="" />
                            </div>

                            <div>
                              <p>{post.body}</p>
                            </div>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </DashboardLout>
    </UserLayout>
  );
};

export default ProfilePage;
